import React from "react";
import "./Signupbutton.css";

const Signupbutton = ({
  children,
  disabled,
  onClick = () => null,
  text = "Click to Sign in",
}) => (
  <button className="Signupbutton" onClick={onClick} disabled={disabled}>
    {children || text}
  </button>
);

export default Signupbutton;
